/* ------------------------------------------------------------------------------------------------------------------------------
Extra bootstrap classes (ДОПОЛНИТЕЛЬНЫЕ КЛАССЫ ДЛЯ СЕТКИ BOOTSTRAP 5.3V)
--------------------------------------------------------------------------------------------------------------------------------*/

.custom-col-5 {
	width: 20%;
	height: inherit;

	@include sm {
		width: 100%;
	}
}

.desktop-vs {
	display: none;
	@media (min-width: 1200px) {
		display: block;
	}
}
.sm-vs {
	display: none;
	@include sm {
		display: block;
	}
}
.xxl-v {
	display: none;
	@media (min-width: 1350px) {
		display: block;
	}
}
.mb-7 {
	margin-bottom: 7px;
}
.fpl-15 {
	padding-left: 15px;
}
.sm-hidden {
	@include sm {
		display: none;
	}
}
.sm-visible {
	display: none;
	@include sm {
		display: flex;
	}
}

.md-visible {
	display: none;
	@include md {
		display: flex;
	}
}
// g0i
.g-0i,
.gx-0i {
	--bs-gutter-x: 0px;
}
.g-0i,
.gy-0i {
	--bs-gutter-y: 0px;
}

