/* ------------------------------------------------------------------------------------------------------------------------------
SEARCH RESULT PAGE
--------------------------------------------------------------------------------------------------------------------------------*/
.searchresult {
	.exp-breadcrumbs{
		margin-bottom: -2px;
	}

	&__filter {
		flex-grow: 1;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		gap: rem(15);
		flex-shrink: 0;

		@include md{
			margin-top: -3px;
			margin-bottom: 6px;
			gap: rem(10);
		}

		&-found {
			color: var(--text);
			font-size: rem(16);
			opacity: 0.5;

			@include md {
				font-size: rem(12);
			}
		}
	}

	.section-flexheader {
		&__title{
			padding-block-start: 0px;
			margin-bottom:7px;
		}
		@include md {
			font-size: rem(24);
			line-height: 98%;
			padding-bottom: rem(15);
		}
	}

	.section-flexheader {
		@include xl2 {
			flex-direction: column;
			align-items: flex-start;
			gap: rem(5);
		}
	}
}
