/* ------------------------------------------------------------------------------------------------------------------------------
Editable input (РЕДАКТИРУЕМЫЙ ИНПУТ И ТЕКСТАРЕА)
--------------------------------------------------------------------------------------------------------------------------------*/
.editable {
	color: rgba(47, 47, 47);
	// Лейбл
	&-text {
		display: block;
		font-weight: 400;
		font-size: 14px;

		margin-bottom: 7px;
	}

	// Обрертка
	&-wrap {
		display: flex;
		align-items: center;
		width: 100%;
		background: var(--white);
		padding: 10px 12px;

		min-height: 48px;

		&--top {
			align-items: start;
		}
	}

	// Инпут и текстареа
	&-input {
		background: var(--white);
		flex-grow: 1;
		font-weight: 400;
		font-size: 14px;
		color: rgba(47, 47, 47, 0.5);

		width: 100%;
		&:focus {
			color: rgba(47, 47, 47, 1);
		}

		&--shorttextarea,
		&--textarea {
			min-height: 210px;
			resize: none;
			line-height: 133%;

			font-weight: 400;
			font-size: 14px;
			color: rgba(47, 47, 47, 0.5);

			&:focus {
				color: rgba(47, 47, 47, 1);
			}

			&::-webkit-scrollbar {
				inline-size: rem(0);
				background-color: transparent;
			}

			&::-webkit-scrollbar-track {
				box-shadow: none;
				background-color: rgb(0, 0, 0, 0.1);
				inline-size: 1px;
			}

			&::-webkit-scrollbar-thumb {
				border: none;
				background-color: rgb(0, 71, 186, 0.5);
			}
		}
		&--shorttextarea {
			min-height: 89px;
		}

		&::placeholder {
			opacity: 0.5;
		}
	}

	// Кнопки справа
	&-buttons {
		display: flex;
		align-items: center;
		gap: 9px;

		&--abs {
			position: relative;

			.editable-button {
				position: absolute;
				right: 0px;
				top: 10px;
			}
		}
	}

	// Одна кнопка
	&-button {
		display: flex;
		align-items: center;
		justify-content: center;
		cursor: pointer;

		height: 100%;
		aspect-ratio: 1/1;

		font-size: 20px;
		font-weight: bold;

		&:hover {
			color: var(--accent);
			svg {
				stroke: var(--accent);
			}
		}
		svg {
			stroke: var(--text);
			aspect-ratio: 1/1;
			width: 22px;

			transition: 0.3s ease-in-out;
		}
	}
	&-group {
		display: inline-flex;
		align-items: center;
		gap: 10px;

		svg {
			stroke-width: 1.5px;
			width: 24px;
		}

		.editable-text {
			margin-bottom: 0;
		}

		margin-bottom: 7px;
		padding-top: 20px;
	}

	&-repeater-block {
		margin-bottom: 20px;
	}
}
